.App {
  text-align: center;
  background-color: #111;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: pink
}

.App-link {
  color: #61dafb;
}

.nav Button{
  background-color: rgb(26, 27, 33);
  padding: 16px !important;
  font-size: 1.3em;
}

.nav Button:hover{
  background-color: rgb(51, 54, 67);
}

.feed {
  display: grid;
  gap: 16px;
  width: 100%;
  justify-content: center;
}

.post {
  width: 100%;
  background-color: rgb(42, 43, 54);
}